import React, { Component } from 'react'
import '../Style/Paginate.css'
import { Pagination, PaginationItem, PaginationLink } from 'reactstrap';


export class Paginate extends Component {
render() {
    return (
        <div className="Paginate">
            <Pagination aria-label="Page navigation example">

                <PaginationItem>
                    <PaginationLink previous href="#" />
                </PaginationItem>

                <PaginationItem>
                    <PaginationLink href="#">
                    1
                    </PaginationLink>
                </PaginationItem>

                <PaginationItem active>
                    <PaginationLink href="#">
                    2
                    </PaginationLink>
                </PaginationItem>

                <PaginationItem>
                    <PaginationLink href="#">
                    3
                    </PaginationLink>
                </PaginationItem>

                <PaginationItem>
                    <PaginationLink href="#">
                    4
                    </PaginationLink>
                </PaginationItem>

                <PaginationItem>
                    <PaginationLink href="#">
                    5
                    </PaginationLink>
                </PaginationItem>

                <PaginationItem>
                    <PaginationLink next href="#" />
                </PaginationItem>

            </Pagination> 
        </div>
    )
}
}

export default Paginate
